<template>
    <div class="xfn-category-list">
        <el-breadcrumb>
            <el-breadcrumb-item to='/main'></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br>
        <el-button type="primary" @click="addCategory"></el-button>
        <el-table :data="categoryList" stripe border style="margin-top:20px;">
            <el-table-column label="" prop="cid"></el-table-column>
            <el-table-column label="" prop="cname"></el-table-column>
            <el-table-column label="">
                <template slot-scope="{row,$index}">
                    <el-button type="success" size="mini" @click="updateCategory(row,$index)"></el-button>
                    <el-button type="danger" size="mini" @click="deleteCategory(row,$index)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>


<script>
export default {
    data(){
        return {
            categoryList:[]
        }
    },
    mounted(){
        var url = this.$store.state.globalSettings.apiUrl+'/admin/category';
        this.$axios.get(url).then((res)=>{
            this.categoryList = res.data;
        }).catch((err)=>{
            console.log(err)
        })
    },
    methods:{
        addCategory(row,index){
            // console.log(row,index)
            this.$prompt(':','',{type:'info'}).then(({value})=>{
                //api
                var url = this.$store.state.globalSettings.apiUrl+'/admin/category';
                this.$axios.post(url,{cname:value}).then((res)=>{
                    if(res.data.code==200){
                        //
                        this.$message.success('')
                        //
                        this.categoryList.push({cid:res.data.cid,cname:value})
                    }else{
                        this.$message.error(''+res.data.msg)
                    }
                }).catch((err)=>{
                    console.log(err)
                })
            }).catch(()=>{

            })
            
        },
        updateCategory(row,index){
            this.$prompt('','',{inputValue:row.cname}).then(({value})=>{
                //axios
                //categoListcname
            }).catch(()=>{

            })
        },
        deleteCategory(row,index){
            // console.log(row,index)
            this.$confirm('','',{type:"warning"}).then(()=>{
                var url = this.$store.state.globalSettings.apiUrl+'/admin/category/'+row.cid;
            this.$axios.delete(url).then((res)=>{
                if(res.data.code==200){ //
                    this.categoryList.splice(index,1);  //
                    this.$message.success('');
                }else{
                    this.$message.error(""+res.data.msg);
                }
            }).catch((res)=>{
                console.log(err);
            })
            }).catch(()=>{

            })
            
        }
    }
}
</script>
