<template>
    <div class="xfn-table-info">
        <el-card shadow="hover">
            <div class="xfn-table" :style="{background:getTableColor(data.status)}">{{data.tid}}{{data.status | tableStatus}}</div>
            <el-button type="success" plain size="mini" @click="showTableDetail"></el-button>
            <el-button type="danger" plain size="mini"></el-button>
        </el-card>
        <!--  -->
        <el-dialog :title="data.tid+''" :visible="dialogTableDetailVisible" :before-close="closeDialogTableDetail">
            <!--  -->
             <el-tabs type="border-card" @tab-click="makeQRcode">
                <el-tab-pane label="">...</el-tab-pane>
                <el-tab-pane label="">
                    <img :src="qrcodeData">
                </el-tab-pane>
            </el-tabs>
            <!--  -->
            <div slot="footer">
                <el-button type="primary" @click="dialogTableDetailVisible=false"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data(){
        return {
            dialogTableDetailVisible: false,
            qrcodeData: ""
        }
    },
    props:['data'],
    methods:{
        getTableColor(status){
            if(status==1) return '#67C23A';
            else if(status==2) return '#E6A23C';
            else if(status==3) return '#F56C6C';
            else return '#909399';
        },
        showTableDetail(){
            // console.log(this.data)//
            this.dialogTableDetailVisible = true;
        },
        closeDialogTableDetail(){
            this.dialogTableDetailVisible = false;
        },
        makeQRcode(){
            //--mountedcanvasel-dialogDOM
            var qrcode = require('qrcode');
            //urlhttp://127.0.0.1:8092/#/7  7
            var tableUrl = this.$store.state.globalSettings.appUrl + '/#/'+this.data.tid;
            // console.log(tableUrl);
            // qrcode.toCanvas(canvas,tableUrl,{width:300,errorCorrectionLevel:'H'});
            //Base64
            qrcode.toDataURL(tableUrl,{width:300,errorCorrectionLevel:"H"},(err,url)=>{
                // console.log('')
                // console.log(url);
                this.qrcodeData = url;
            })
        }
    }
    
}
</script>

<style lang="scss">
    .xfn-table-info{
        padding: 3px 0px;
        text-align: center;

        .xfn-table{
            width: 90%;
            height: 120px;
            border: 1px solid #aaa;
            box-shadow: 3px -4px 5px #666;
            margin: 20px auto;
            border-radius: 70%;
            line-height: 120px;
        }
    }
</style>
