<template>
    <div class="xfn-dish-add">
        <h3></h3>
        <el-form label-width="100px">
            <el-form-item label="">
                <el-upload class="xfn-uploader" :action='uploadAction' :on-success="doUploadSucc" name="dishImg" :show-file-list="false">
                    <img v-if="imageUrl"  :src="imageUrl">
                    <i v-else class="el-icon-plus"></i>
                </el-upload>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    data(){
        return {
            imageUrl: '', //
            uploadAction: this.$store.state.globalSettings.apiUrl+'/admin/dish/image',  // API
            formData: {
                title:'',
                imgUrl: '',  //
                price:'',
                detail:'',
                categotyId:'' 
            }
        }
    },
    methods:{
        doUploadSucc(res,file){
            //
            //res:
            //file:input[type=file]
            console.log(res);
            this.formData.imgUrl = res.fileName;
            this.imageUrl = URL.createObjectURL(file.raw);//DataUrl 
            console.log(this.imageUrl)
        }
    }
}
</script>


<style lang="scss">
.xfn-uploader{
    .el-upload{
        border:1px dotted #aaa;
        border-radius: 3px;
        cursor: pointer;
        width: 250px;
        height: 177px;
        overflow: hidden;
        &:hover{
            border-color: #409EFF; 
        }
        .el-icon-plus{
            line-height: 177px;
            font-size: 70px;
            color: #ccc;
        }
    }

    img{
        max-width: 100%;
    }
}
</style>
